import {managerList,managerCount} from '../../utils/request'
const state = {
  managerList :[],
  size:2,
  page:1,
  count:0
}

const mutations = {
  changeManagerList(state,arr){
    state.managerList = arr
  },
  changeCount(state,count){
    state.count = count
  },
  changePage(state,page){
    console.log(page);
    state.page = page
  }
}

const actions = {
  managerListActions(context){
    var params = {
      size:context.state.size,
      page:context.state.page,
    }
    // 发起请求
    managerList(params).then(res=>{
      if((res.data.list == null || res.data.list.length == 0) && context.state.page > 1 ){
        var page = context.state.page - 1;
        // 修改page的值
        context.commit('changePage',page);
        //自調
        context.dispatch('managerListActions');
        return
      }
      context.commit('changeManagerList',res.data.list)
    })
  },
  countActions(context){
    managerCount().then(res=>{
      context.commit('changeCount',res.data.list[0].total)
    })
  },
  pageActions(context,page){
    context.commit('changePage',page)
  }
}

const getters = {
  managerList(state){
    return state.managerList
  },
  count(state){
    return state.count
  }
}

export default {
  state,
  mutations,
  actions,
  getters,
  namespaced:true,
}



