import {menuList} from '../../utils/request'
const state = {
  menuList:[],
}

const mutations = {
  // 修改menuList数据
  changeMenuList(state,arr){
    state.menuList = arr
  }
}

const actions = {
  menuListActions(context){
    // 发起请求
    menuList({istree:true}).then(res=>{
      context.commit('changeMenuList',res.data.list)
    })
  }
}

const getters = {
  menuList(state){
    return state.menuList
  }
}


export default {
  state,
  mutations,
  actions,
  getters,
  namespaced:true,
}



