import { getGoods, goodsCount } from "../../utils/request";

const state = {
  goodsList:[],
  size:2,
  page:1,
  count:0
}

const mutations = {
  changeGoodsList(state,arr){
    state.goodsList = arr;
  },
  changeCount(state,num){
    state.count = num
  },
  changePage(state,p){
    state.page = p
  }
}

const actions = {
  goodsListActions(context,value){
    var params = null;
    if(value !== null){
      params = {
        size:context.state.size,
        page:context.state.page
      }
    }
    getGoods(params).then(res=>{
      if((res.data.list == null || res.data.list.length==0) && context.state.page > 1){
        var p = context.state.page -1;
        context.commot('changePage',p);
        context.dispatch("goodsListActions",true)
        return
      }
      context.commit('changeGoodsList',res.data.list)
    })
  },
  countActions(context){
    goodsCount().then(res=>{
      context.commit('changeCount',res.data.list[0].total)
    })
  },
  pageActions(context,p){
    context.commit('changePage',p)
  }
}

const getters = {
  goodsList(state){
    return state.goodsList
  },
  count(state){
    return state.count
  }
}


export default {
  state,mutations,actions,getters,
  namespaced:true,
}



